'use client'; // Necesario para usar useState y manejo de eventos

import { useState } from 'react';
import Link from 'next/link';
import styles from '../app/page.module.css'; 

// Datos de los enlaces de navegaci√≥n
const navItems = [
    { name: 'Portada', href: '' },
    { name: 'Ligas', href: '/ligas', isDropdown: true }, // Marcado como Dropdown
    { name: 'Resultados', href: '/resultados' },
    { name: 'Equipos', href: '/equipos' },
    { name: 'Calendario', href: '/calendario' },
];

// Datos inventados de las ligas
const ligasDropdown = {
    'futbol': [
        { name: 'Liga Campus Isla Teja', id: 'liga-isla' },
        { name: 'Copa Austral Mixta', id: 'copa-austral' },
        { name: 'Torneo Nocturno Valdivia', id: 'torneo-valdivia' },
    ],
    'voleibol': [
        { name: 'Campeonato Patag√≥nico Indoor', id: 'camp-patagonico' },
        { name: 'Volei Playa Rec. (Verano)', id: 'playa-verano' },
    ],
    'basquetbol': [
        { name: 'Copa UACh Tres Puntos', id: 'copa-puntos' },
        { name: 'Liga 3x3 Express', id: 'liga-express' },
    ]
};

export default function SportSubNav({ sportNameId, sportDisplayName }) {
    // Estado para controlar la visibilidad del dropdown de Ligas
    const [isLigasOpen, setIsLigasOpen] = useState(false);
    const currentLigas = ligasDropdown[sportNameId] || [];

    return (
        <nav className={styles.subNavContainer}>
            <div className={styles.subNavHeader}>
                {/* √çconos din√°micos */}
                <span className={styles.subNavIcon}>{sportNameId === 'futbol' ? '‚öΩ' : sportNameId === 'voleibol' ? 'üèê' : 'üèÄ'}</span>
                <span className={styles.subNavSportName}>{sportDisplayName}</span>
            </div>
            
            <div className={styles.subNavLinks}>
                {navItems.map((item) => (
                    <div 
                        key={item.name} 
                        className={styles.navItemWrapper}
                        // MANEJO DE HOVER: Abre el men√∫ al pasar el rat√≥n (Desktop)
                        onMouseEnter={() => item.isDropdown && setIsLigasOpen(true)}
                        onMouseLeave={() => item.isDropdown && setIsLigasOpen(false)}
                    >
                        {item.isDropdown ? (
                            <div 
                                className={`${styles.navItem} ${currentLigas.length > 0 ? styles.hasDropdown : ''}`}
                                // üí° SOLUCI√ìN: L√ìGICA DE CLIC: Alterna el estado del men√∫ (M√≥vil/T√°ctil)
                                onClick={() => setIsLigasOpen(!isLigasOpen)} 
                            >
                                {item.name}
                                {/* DROPDOWN MENU */}
                                {isLigasOpen && currentLigas.length > 0 && (
                                    <div className={styles.dropdownMenu}>
                                        {currentLigas.map((liga) => (
                                            <Link 
                                                key={liga.id}
                                                // Ejemplo de ruta: /futbol/ligas/liga-isla
                                                href={`/${sportNameId}${item.href}/${liga.id}`} 
                                                className={styles.dropdownItem}
                                                onClick={(e) => {
                                                    // Es importante detener la propagaci√≥n del evento
                                                    // para que el clic en el Link no active el onClick del div padre (que lo cerrar√≠a y abrir√≠a al mismo tiempo)
                                                    e.stopPropagation(); 
                                                    setIsLigasOpen(false); // Cierra el men√∫ al seleccionar una liga
                                                }} 
                                            >
                                                {liga.name}
                                            </Link>
                                        ))}
                                    </div>
                                )}
                            </div>
                        ) : (
                            // Enlaces de navegaci√≥n normal
                            <Link 
                                href={`/${sportNameId}${item.href}`} 
                                className={`${styles.navItem} ${item.name === 'Portada' ? styles.activeNavItem : ''}`} 
                            >
                                {item.name}
                            </Link>
                        )}
                    </div>
                ))}
            </div>
        </nav>
    );
}